<template>
    <div id="linkman">
      <!-- -->
      <div id="title">
        <mt-header title="">
          <router-link to="/head" slot="left">
            <mt-button icon="back"></mt-button>
          </router-link>
        </mt-header>
      </div>
      <!---->
      <div id="linkmanList">
        <mt-loadmore  :bottom-method="loadBottom" :bottomPullText='bottomText'
                      :bottom-all-loaded="allLoaded" ref="loadmore">
          <div  v-for="(item,index) in list" :key="index">
            <mt-cell class="cell" :title="item.name"  :to="item.url"  is-link  ></mt-cell>
          </div>
        </mt-loadmore>
      </div>

    </div>
</template>

<script>
    export default {
        name: "linkman",
      data(){
        return{
          bottomText: '...',
          allLoaded:false,
          pageNo:1,
          pageSize:10,
          totalPage:100,
          list:[
            {
              name:"",
              url:"/talk?id=2348"
            },
          ],
        }
      },
      methods: {
        loadBottom: function () {
          setTimeout(console.log(""), 2000);
          var x = {
              name:"",
              url:"/talk?id=2348"
            };
          this.list.push(x)
          //this.allLoaded = true;// 
          this.$refs.loadmore.onBottomLoaded();
        },
        more: function () {
          // 
          if (this.totalPage == 1) {
            this.pageNo = 1;
            this.allLoaded = true;
          } else {
            this.pageNo = this.pageNo + 1;
            this.allLoaded = false;
          }
        },
        isHaveMore:function(){
          // 
          //this.allLoaded = false; //true
          if(this.pageNo == this.totalpage){
            this.allLoaded = true;
          }
        },
      },

    }
</script>

<style scoped>

</style>



